.App {
	width: 93%;
	display: flex;
	flex-direction: column;
	margin: 0 auto;
	align-items: center;
	justify-content: center;
	font-family: 'Poppins';
	color: #fff;
}

h1 {
	font-family: 'Niconne';
	font-size: 4rem;
	color: #fff;
	margin: 3rem 0 1rem 0;
}

body {
	background: #2b2b2b;
}

footer {
	color: #fff;
	justify-content: center;
	text-align: center;
	font-family: 'Niconne';
	background-color: #1e1e1e;
	height: 100px;
	font-size: 2rem;
	padding: 3rem;
	margin-top: 5rem;
}

.text-white {
	margin-bottom: 3rem;
	color: #fff;
}

a {
	text-decoration: none;
	color: #fff;
}

.allproject {
	margin: 7rem;
}
